import React, { useState, useEffect } from 'react';
import './App.css';
import Post from './Post';
import { db, auth } from './firebase';
import { makeStyles } from '@material-ui/core/styles';
import Modal from '@material-ui/core/Modal';
import {Button, Input} from '@material-ui/core';
import ImageUpload from './ImageUpload';
import InstagramEmbed from 'react-instagram-embed';



function getModalStyle() {
  const top = 50 ;
  const left = 50;

  return {
    top: `${top}%`,
    left: `${left}%`,
    transform: `translate(-${top}%, -${left}%)`,
  };
}

const useStyles = makeStyles((theme) => ({
  paper: {
    position: 'absolute',
    width: 400,
    backgroundColor: theme.palette.background.paper,
    border: '2px solid #000',
    boxShadow: theme.shadows[5],
    padding: theme.spacing(2, 4, 3),
  },
}));

function App() {
  const classes = useStyles();
  const [modalStyle] = React.useState(getModalStyle);
  const [posts, setPosts] = useState([]);
  const [open, setOpen] = useState(false);
  const [openSignIn , setOpenSignIn] = useState(false);
  const [username, setUsername] = useState('');
  const [password, setPassword] = useState('');
  const [email, setEmail] = useState('');
  const [user, setUser] = useState (null);

  useEffect(() => {
      const unsubscribe = auth.onAuthStateChanged((authUser) => {
        if(authUser) {
            //user has logged in
            console.log(authUser);
            setUser(authUser);
        }else{
          //user has logged out
          setUser (null);
        }
      })
      return () => {
        //perform some cleanup actions
      }
    }, [user, username])

     //useEffect runs a piece of code on specific condition 
      useEffect(() =>{
        //this is were code runs
        db.collection('posts').orderBy('timeStamp', 'desc').onSnapshot(snapshot => {
          //every time a new post is added this code is fired
          setPosts(snapshot.docs.map(doc => ({
            id:doc.id,
            post:doc.data()
          })));
        })
      }, []);

    
 
 const signUp = (event => {
  event.preventDefault();

  auth.createUserWithEmailAndPassword (email, password)
  .then((authUser =>{
    return authUser.user.updateProfile({
      displayName:username,
    })
  }))
  .catch((error) => alert(error.message));

  setOpen(false);
 })

 const signIn = (event => {
    event.preventDefault();

    auth
      .signInWithEmailAndPassword(email, password)
      .catch((error) => alert(error.message))

      setOpenSignIn(false);
 })
  return (
    <div className="app">
      <Modal
        open={open}
        onClose={()=> setOpen(false)}
      >
        <div style={modalStyle} className={classes.paper}>
            <form className = 'app__signUp'>
                <center>
                   <img 
                    className = 'app__headerImage' 
                    src="data:image/png;base64,"
                    alt='instagram logo'/>
                </center>      
                  <Input
                  placeholder='username'
                  type='text'
                  value={username}
                  onChange= {(e) => setUsername(e.target.value)}
                  />
                 <Input
                  placeholder='email'
                  type='text'
                  value={email}
                  onChange = {(e) => setEmail(e.target.value)}
                  />
                  <Input
                  placeholder='password'
                  type='password'
                  value={password}
                  onChange = {(e) => setPassword(e.target.value)}
                  />  
                  <Button type='submit' onClick={signUp}>Sign Up</Button>
            </form>
      </div>

      </Modal>
      <Modal
        open={openSignIn}
        onClose={()=> setOpenSignIn(false)}
      >
        <div style={modalStyle} className={classes.paper}>
            <form className = 'app__signUp'>
                <center>
                   <img 
                    className = 'app__headerImage' 
                    src="data:image/png;base64,"
                    alt='instagram logo'/>
                </center>      
                 <Input
                  placeholder='email'
                  type='text'
                  value={email}
                  onChange = {(e) => setEmail(e.target.value)}
                  />
                  <Input
                  placeholder='password'
                  type='password'
                  value={password}
                  onChange = {(e) => setPassword(e.target.value)}
                  />  
                  <Button type='submit' onClick={signIn}>Sign In</Button>
            </form>
      </div>
      </Modal>

      <div className='app__header'>
        <img 
        className = 'app__headerImage' 
        src="data:image/png;base64,"
        alt='instagram logo'/>
        
        {user ? (
          <Button onClick={() =>auth.signOut()}>Log Out</Button>
        ) : (
          <div className = 'app__loginContainer'>
          <Button onClick={() =>setOpenSignIn (true)}>Sign In</Button>
          <Button onClick={() =>setOpen (true)}>Sign Up</Button>
          </div>  
          
        )}
      </div>

      <div className='app__posts'>
      <div className='app__posts--left'>
         {
        posts.map(({ id, post}) => (
        <Post  postId={id} key={id}  user={user} username={post.username} caption={post.caption} imageUrl={post.imageUrl} />
      ))
      } 
      </div>
      <div className='app__posts--right'>
        <InstagramEmbed
          url='https://www.instagram.com/p/B60m0SzHbeG/?utm_source=ig_web_copy_link'
          maxWidth={320}
          hideCaption={false}
          containerTagName='div'
          protocol=''
          injectScript
          onLoading={() => {}}
          onSuccess={() => {}}
          onAfterRender={() => {}}
          onFailure={() => {}}
        />
    </div>
      </div>

      
      
      {user?.displayName ? (
        <ImageUpload username={user.displayName}/>
        ) : (
          <h3> Sorry you need to login to upload</h3>
        )}

       {
     //    <Post username= 'Nabil' caption='Attending Accenture training program' imageUrl='https://cdn.evilmartians.com/front/posts/optimizing-react-virtual-dom-explained/cover-a1d5b40.png'/> 
     // <Post username= 'Nagarjun' caption='Got selected for capegemini' imageUrl='https://cdn.auth0.com/blog/illustrations/reactjs.png'/> 
     // <Post username= 'Clement' caption='Want to be a hacker' imageUrl='https://blog.addthiscdn.com/wp-content/uploads/2014/11/addthis-react-flux-javascript-scaling.png'/> 
        // HEADER
        // POSTS
        // POSTS
      }
    </div>
  );
}

export default App;
